const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			inputValue: '',
			sercherStorage: [],
			storageFlag: false, // 显示搜索记录标志位
			mask: true,
			mask1: false,
			itemList: [],
			current_page: 1,
			imgUrl: ''
		};
	},
	onLoad() {
		this.imgUrl = helper.imgUrl;
		this.openLocationsercher();
	},

	methods: {
		// 跳转详情
		goDetail(e){
			uni.navigateTo({
				url:`/pages/productDetail?id=${e}`
			})
		},
		// 获取输入框的输入信息
		bindInput(e) {
			this.inputValue = e.detail.value;
			if (this.inputValue == '') {
				this.mask = true;
			}
		},
		// // 清楚历史记录单条
		clearList(e) {
			console.log(e, '++++');
			let index = e;
			this.sercherStorage.splice(index, 1);
			//将搜索记录更新到缓存
			var searchData = this.sercherStorage;
			uni.setStorageSync('searchData', searchData);
			if (this.sercherStorage.length == 0) {
				this.storageFlag = false;
			} else {
				this.storageFlag = true;
			}
		},
		// // 清楚缓存历史并关闭历史搜索框
		clearSearchStorage() {
			uni.removeStorageSync('searchData');
			this.sercherStorage = [];
			this.storageFlag = false;
		},
		// // 点击缓存搜索列表
		tapSercherStorage(e) {
			var index = e;
			this.inputValue = index;
		},
		// //添加搜索记录并搜索
		setSearchStorage(e) {
			this.inputValue = e;
			if (this.inputValue != '') {
				//将搜索记录更新到缓存
				var searchData = this.sercherStorage;
				searchData.unshift(this.inputValue);
				searchData = [...new Set(searchData)];
				searchData = searchData.splice(0, 6);
				console.log(searchData);
				uni.setStorageSync('searchData', searchData);
				this.storageFlag = true;
				this.mask = false;
				this.openLocationsercher();
				this.itemList = [];
				this.current_page = 1;
				this.getListData();
			}
		},
		// // 打开历史记录列表
		openLocationsercher() {
			console.log(uni.getStorageSync('searchData'));
			this.sercherStorage = uni.getStorageSync('searchData') || []; //调用API从本地缓存中获取数据
			if (this.sercherStorage == '') {
				this.storageFlag = false;
			} else {
				this.storageFlag = true;
			}
		},
		getListData() {
			uni.request({
				url: `${helper.requestUrl}/holes/search`,
				// header: {
				// 	authorization: this.$parent.globalData.token,
				// },
				method: 'GET',
				data: {
					keywords: this.inputValue,
					page: this.current_page,
					page_size: 20
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.itemList = this.itemList.concat(res.data.data);
						// 当前页码
						this.current_page = res.data.current_page;
						// 总页码
						this.last_page = res.data.last_page;
					} else if (res.data.status_code == 400) {
						this.itemList = '';
					}
				}
			});
		}
	}
};
